import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import CustomerDetails from '../components/page/CustomerDetails';
import School from '../components/admin/School';
import Courses from '../components/admin/Courses';
import CourseDetails from '../components/admin/CourseDetails';
import DegreePrograms from '../components/admin/DegreePrograms';
import Departments from '../components/admin/Departments';
import Faculty from '../components/admin/Faculty';
import GradeReport from '../components/admin/GradeReport';
import Graduations from '../components/admin/Graduations';
import StudentActivity from '../components/admin/StudentActivity';
import Students from '../components/admin/Students';
import StudentDetails from '../components/admin/StudentDetails';

const DashboardRoutes = () => (
  <Switch>
      <Route path="/pages/customer-details" exact component={CustomerDetails} />
      <Route path="/admin/school" exact component={School} />
      <Route path="/admin/courses" exact component={Courses} />
      <Route path="/admin/course-details" exact component={CourseDetails} />
      <Route path="/admin/degree-programs" exact component={DegreePrograms} />
      <Route path="/admin/departments" exact component={Departments} />
      <Route path="/admin/faculty" exact component={Faculty} />
      <Route path="/admin/grade-report" exact component={GradeReport} />
      <Route path="/admin/graduations" exact component={Graduations} />
      <Route path="/admin/student-activity" exact component={StudentActivity} />
      <Route path="/admin/students" exact component={Students} />
      <Route path="/admin/student-details" exact component={StudentDetails} />
      {/*Redirect*/}
      <Redirect to="/errors/404" />
  </Switch>
);

export default DashboardRoutes;
