import React, { useState, useEffect, useContext, Fragment, createRef } from 'react';
import { Alert, Card, CardBody, Col, Row } from 'reactstrap';
import FalconCardHeader from '../common/FalconCardHeader';
import { studentsApi } from '../../config';
import { isIterableArray } from '../../helpers/utils';
import AppContext from '../../context/Context';
import axios from 'axios';
import Loader from '../common/Loader';
import moment from 'moment';
import { toast } from 'react-toastify';
import paginationFactory, { PaginationProvider } from 'react-bootstrap-table2-paginator';
import BootstrapTable from 'react-bootstrap-table-next';
import { Link } from 'react-router-dom';

const columns = [
  {
    dataField: 'id',
    text: 'id',
    formatter: customerFormatter,
    sort: true
  },  
  {
    dataField: 'first_name',
    text: 'first_name',
    sort: true
  },
  {
    dataField: 'middle_name',
    text: 'middle_name',
    sort: true
  },
  {
    dataField: 'last_name',
    text: 'last_name',
    sort: true
  },  
  {
    dataField: 'gender',
    text: 'gender',
    sort: true
  },  
  {
    dataField: 'date_of_birth',
    text: 'date_of_birth',
    sort: true
  },   
  {
    dataField: 'bio',
    text: 'Bio',
    sort: false
  }, 
  {
    dataField: 'degree_program',
    text: 'degree_program',
    sort: false
  }, 
  {
    dataField: 'student_type',
    text: 'student_type',
    sort: true
  }, 
  {
    dataField: 'status_code',
    text: 'status_code',
    sort: true
  }, 
  {
    dataField: 'current_level',
    text: 'current_level',
    sort: true
  }, 
  {
    dataField: 'date_accepted',
    text: 'date_accepted',
    sort: false
  }, 
  {
    dataField: 'date_graduated',
    text: 'date_graduated',
    sort: false
  }, 
  {
    dataField: 'country',
    text: 'country',
    sort: true
  }, 
  {
    dataField: 'address_1',
    text: 'Address',
    sort: false
  },     
  {
    dataField: 'city',
    text: 'City',
    sort: false
  },     
  {
    dataField: 'stateprovince',
    text: 'State',
    sort: false
  },     
  {
    dataField: 'zipcode',
    text: 'Zip',
    sort: false
  },     
  {
    dataField: 'email',
    text: 'Email',
    sort: false
  },    
  {
    dataField: 'phone_number',
    text: 'Phone',
    sort: false
  },    
  {
    dataField: 'timestamp',
    text: 'Created',
    sort: false
  }, 
];

const customerFormatter = id => (
  <Link to={`admin/student-details?id=${id}`} className="font-weight-semi-bold">
    {id}
  </Link>
);

const Students = () => {
  // State
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);

  // Context
  const { authToken } = useContext(AppContext);

  let table = createRef();

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      if (!authToken) {
        console.log("authToken missing in fetchData");
        return;
      }

      try {
        axios.defaults.headers.common = {
          'Authorization': 'Bearer ' + authToken
        };

        const url = `${studentsApi}?page=1&per_page=1000`;
        const response = await axios.get(url);
        console.debug(">>> response.data >>> ", response.data);
        
        if (response.data && response.data.items) {
          console.log(">>> items >>> ", response.data.items);
          const dataSet = response.data.items;
          const items = [];
          for (const dataItem of dataSet) {
            const id = dataItem.id;
            const first_name = (dataItem.first_name) ? dataItem.first_name : "";
            const middle_name = (dataItem.middle_name) ? dataItem.middle_name : "";
            const last_name = (dataItem.last_name) ? dataItem.last_name : "";
            const gender = (dataItem.gender) ? dataItem.gender : "";
            const date_of_birth = (dataItem.date_of_birth) ? dataItem.date_of_birth : "";
            const bio = (dataItem.bio) ? dataItem.bio : "";
            const degree_program = (dataItem.degree_program) ? dataItem.degree_program : "";
            const student_type = (dataItem.student_type) ? dataItem.student_type : "";
            const status_code = (dataItem.status_code) ? dataItem.status_code : "";
            const current_level = (dataItem.current_level) ? dataItem.current_level : "";
            const date_accepted = (dataItem.date_accepted) ? dataItem.date_accepted : "";
            const date_graduated = (dataItem.date_graduated) ? dataItem.date_graduated : "";
            const country = (dataItem.country) ? dataItem.country : "";
            const address_1 = (dataItem.address_1) ? dataItem.address_1 : "";
            const city = (dataItem.city) ? dataItem.city : "";
            const stateprovince = (dataItem.stateprovince) ? dataItem.stateprovince : "";
            const zipcode = (dataItem.zipcode) ? dataItem.zipcode : "";
            const email = (dataItem.email) ? dataItem.email : "";
            const phone_number = (dataItem.phone_number) ? dataItem.phone_number : "";
            const timestamp = (dataItem.timestamp) ? dataItem.timestamp : "";
            const item = {
              "id": id,
              "first_name": first_name,
              "middle_name": middle_name,
              "last_name": last_name,
              "gender": gender,
              "date_of_birth": date_of_birth,
              "bio": bio,
              "degree_program": degree_program,
              "student_type": student_type,
              "status_code": status_code,
              "current_level": current_level, 
              "date_accepted": date_accepted, 
              "date_graduated": date_graduated,
              "country": country,
              "address_1": address_1,
              "city": city,
              "stateprovince": stateprovince,
              "zipcode": zipcode,
              "email": email,
              "phone_number": phone_number,
              "timestamp": timestamp
            }
            items.push(item);
          }
          setData(items);
        }
      } catch(err) {
        console.log(err);
        toast.error(err.toString());
      }
      setLoading(false);
    };
    fetchData();
  }, []);  

  return (
    <Fragment>
      <Card>
        <FalconCardHeader title="Students" />
        <CardBody className="fs--1 p-0">
          {loading ? (
            <Loader />
          ) : (
              <div className="table-responsive">
                <BootstrapTable
                  ref={table}
                  bootstrap4
                  keyField="id"
                  data={data}
                  columns={columns}
                  bordered={false}
                  classes="table-dashboard table-sm fs--1 border-bottom mb-0 table-dashboard-th-nowrap"
                  rowClasses="btn-reveal-trigger"
                  headerClasses="bg-200 text-900"
                />
              </div>
            )}
        </CardBody>
      </Card>
    </Fragment>
  );
};

export default Students;
