import React, { useState, useEffect, useContext, Fragment, createRef } from 'react';
import { Alert, Card, CardBody, Col, Row } from 'reactstrap';
import FalconCardHeader from '../common/FalconCardHeader';
import { coursesApi } from '../../config';
import { isIterableArray } from '../../helpers/utils';
import AppContext from '../../context/Context';
import axios from 'axios';
import Loader from '../common/Loader';
import moment from 'moment';
import { toast } from 'react-toastify';
import paginationFactory, { PaginationProvider } from 'react-bootstrap-table2-paginator';
import BootstrapTable from 'react-bootstrap-table-next';
import { Link } from 'react-router-dom';

const columns = [
  {
    dataField: 'id',
    text: 'id',
    formatter: customerFormatter,
    sort: true
  },  
  {
    dataField: 'course_name',
    text: 'course_name',
    sort: true
  },
  {
    dataField: 'course_desc',
    text: 'course_desc',
    sort: true
  },
  {
    dataField: 'course_link',
    text: 'course_link',
    sort: true
  },  
  {
    dataField: 'course_level',
    text: 'course_level',
    sort: true
  },  
  {
    dataField: 'credit_hours',
    text: 'credit_hours',
    sort: true
  },   
  {
    dataField: 'degree_name',
    text: 'degree_name',
    sort: false
  }, 
  {
    dataField: 'timestamp',
    text: 'Created',
    sort: false
  }, 
];

const customerFormatter = id => (
  <Link to={`admin/course-details?id=${id}`} className="font-weight-semi-bold">
    {id}
  </Link>
);

const Courses = () => {
  // State
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);

  // Context
  const { authToken } = useContext(AppContext);

  let table = createRef();

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      if (!authToken) {
        console.log("authToken missing in fetchData");
        return;
      }

      try {
        axios.defaults.headers.common = {
          'Authorization': 'Bearer ' + authToken
        };

        const url = `${coursesApi}?page=1&per_page=1000`;
        const response = await axios.get(url);
        console.debug(">>> response.data >>> ", response.data);
        
        if (response.data && response.data.items) {
          console.log(">>> items >>> ", response.data.items);
          const dataSet = response.data.items;
          const items = [];
          for (const dataItem of dataSet) {
            const id = dataItem.id;
            const course_name = (dataItem.course_name) ? dataItem.course_name : "";
            const course_desc = (dataItem.course_desc) ? dataItem.course_desc : "";
            const course_link = (dataItem.course_link) ? dataItem.course_link : "";
            const course_level = (dataItem.course_level) ? dataItem.course_level : "";
            const credit_hours = (dataItem.credit_hours) ? dataItem.credit_hours : "";
            const degree_name = (dataItem.degree_name) ? dataItem.degree_name : "";
            const timestamp = (dataItem.timestamp) ? dataItem.timestamp : "";
            const item = {
              "id": id,
              "course_name": course_name,
              "course_desc": course_desc,
              "course_link": course_link,
              "course_level": course_level,
              "credit_hours": credit_hours,
              "degree_name": degree_name,
              "timestamp": timestamp
            }
            items.push(item);
          }
          setData(items);
        }
      } catch(err) {
        console.log(err);
        toast.error(err.toString());
      }
      setLoading(false);
    };
    fetchData();
  }, []);  

  return (
    <Fragment>
      <Card>
        <FalconCardHeader title="Students" />
        <CardBody className="fs--1 p-0">
          {loading ? (
            <Loader />
          ) : (
              <div className="table-responsive">
                <BootstrapTable
                  ref={table}
                  bootstrap4
                  keyField="id"
                  data={data}
                  columns={columns}
                  bordered={false}
                  classes="table-dashboard table-sm fs--1 border-bottom mb-0 table-dashboard-th-nowrap"
                  rowClasses="btn-reveal-trigger"
                  headerClasses="bg-200 text-900"
                />
              </div>
            )}
        </CardBody>
      </Card>
    </Fragment>
  );
};

export default Courses;
