import React, { useState, useEffect, useContext, Fragment, createRef } from 'react';
import { Alert, Card, CardBody, Col, Row } from 'reactstrap';
import FalconCardHeader from '../common/FalconCardHeader';
import { schoolsApi } from '../../config';
import { isIterableArray } from '../../helpers/utils';
import AppContext from '../../context/Context';
import axios from 'axios';
import Loader from '../common/Loader';
import moment from 'moment';
import { toast } from 'react-toastify';
import paginationFactory, { PaginationProvider } from 'react-bootstrap-table2-paginator';
import BootstrapTable from 'react-bootstrap-table-next';
import { Link } from 'react-router-dom';

const columns = [
  {
    dataField: 'id',
    text: 'ID',
    formatter: customerFormatter,
    sort: true
  },  
  {
    dataField: 'school_name',
    text: 'School Name',
    sort: true
  },
  {
    dataField: 'school_type',
    text: 'School Type',
    sort: true
  },
  {
    dataField: 'school_description',
    text: 'Description',
    sort: true
  },  
  {
    dataField: 'date_founded',
    text: 'Date Founded',
    sort: true
  },  
  {
    dataField: 'accrediting_organization',
    text: 'Accrediting Org',
    sort: true
  },   
  {
    dataField: 'bio',
    text: 'Bio',
    sort: false
  },   
  {
    dataField: 'address_1',
    text: 'Address',
    sort: false
  },     
  {
    dataField: 'city',
    text: 'City',
    sort: false
  },     
  {
    dataField: 'stateprovince',
    text: 'State',
    sort: false
  },     
  {
    dataField: 'zipcode',
    text: 'Zip',
    sort: false
  },     
  {
    dataField: 'email',
    text: 'Email',
    sort: false
  },    
  {
    dataField: 'phone_number',
    text: 'Phone',
    sort: false
  },    
  {
    dataField: 'timestamp',
    text: 'Created',
    sort: false
  }, 
];

const customerFormatter = id => (
  <Link to={`admin/school?id=${id}`} className="font-weight-semi-bold">
    {id}
  </Link>
);

const School = () => {
  // State
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);

  // Context
  const { authToken } = useContext(AppContext);

  let table = createRef();

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      if (!authToken) {
        console.log("authToken missing in fetchData");
        return;
      }

      try {
        axios.defaults.headers.common = {
          'Authorization': 'Bearer ' + authToken
        };

        const url = `${schoolsApi}?page=1&per_page=1000`;
        const response = await axios.get(url);
        console.debug(">>> response.data >>> ", response.data);
        
        if (response.data && response.data.items) {
          console.log(">>> items >>> ", response.data.items);
          const dataSet = response.data.items;
          const items = [];
          for (const dataItem of dataSet) {
            const id = dataItem.id;
            const school_name = (dataItem.school_name) ? dataItem.school_name : "";
            const school_description = (dataItem.school_description) ? dataItem.school_description : "";
            const school_type = (dataItem.school_type) ? dataItem.school_type : "";
            const date_founded = (dataItem.date_founded) ? dataItem.date_founded : "";
            const accrediting_organization = (dataItem.accrediting_organization) ? dataItem.accrediting_organization : "";
            const bio = (dataItem.bio) ? dataItem.bio : "";
            const address_1 = (dataItem.address_1) ? dataItem.address_1 : "";
            const city = (dataItem.city) ? dataItem.city : "";
            const stateprovince = (dataItem.stateprovince) ? dataItem.stateprovince : "";
            const zipcode = (dataItem.zipcode) ? dataItem.zipcode : "";
            const email = (dataItem.email) ? dataItem.email : "";
            const phone_number = (dataItem.phone_number) ? dataItem.phone_number : "";
            const timestamp = (dataItem.timestamp) ? dataItem.timestamp : "";
            const item = {
              "id": id,
              "school_name": school_name,
              "school_type": school_type,
              "school_description": school_description,
              "date_founded": date_founded,
              "accrediting_organization": accrediting_organization,
              "bio": bio,
              "address_1": address_1,
              "city": city,
              "stateprovince": stateprovince,
              "zipcode": zipcode,
              "email": email,
              "phone_number": phone_number,
              "timestamp": timestamp
            }
            items.push(item);
          }
          setData(items);
        }
      } catch(err) {
        console.log(err);
        toast.error(err.toString());
      }
      setLoading(false);
    };
    fetchData();
  }, []);  

  return (
    <Fragment>
      <Card>
        <FalconCardHeader title="Schools" />
        <CardBody className="fs--1 p-0">
          {loading ? (
            <Loader />
          ) : (
              <div className="table-responsive">
                <BootstrapTable
                  ref={table}
                  bootstrap4
                  keyField="id"
                  data={data}
                  columns={columns}
                  bordered={false}
                  classes="table-dashboard table-sm fs--1 border-bottom mb-0 table-dashboard-th-nowrap"
                  rowClasses="btn-reveal-trigger"
                  headerClasses="bg-200 text-900"
                />
              </div>
            )}
        </CardBody>
      </Card>
    </Fragment>
  );
};

export default School;
