const CustomerDetails = () => {
  // State
  const [data, setData] = useState(null);
  // Context
  const { authToken } = useContext(AppContext);  
  console.log(">>> CustomerDetails >>> authToken >>> ", authToken);

  useEffect(() => {
    const fetchData = async () => {
      if (!authToken) {
        console.log("authToken missing in fetchData");
        return;
      }      
      axios.defaults.headers.common = {
        'Authorization': 'Bearer ' + authToken
      };      
      try {
        const search = window.location.search;
        const params = new URLSearchParams(search);
        const id = params.get('id');

        if (!id) {
          console.log("id missing in fetchData");
          return;
        }           

        const url = `${applicantsApi}/${id}`;
        const applicantData = await axios.get( url );
        if (applicantData.data) {
          setData(applicantData.data);
        }
      } catch(err) {
        console.warn(">>> handleAcceptedClicked >>> err >>> ", err);
        const toastError = `Failed to load Applicant: ${err}`;
        toast.danger(toastError);
      }
    };
    fetchData();
  }, []);   

  return (
    <Fragment>
      <CustomerSummary data={data} token={authToken} />
      <CustomerDetail data={data} />
    </Fragment>
  );
};

