const CustomerSummary = (props) => {
  console.log(">>> CustomerSummary >>> props >>> ", props);
  const data = (props.data) ? props.data : undefined;
  const token = (props.token) ? props.token : undefined;
  return (
    <Card className="mb-3">
      {!data ? (
        <Loader />
      ) : (
        <Fragment>
          <CardHeader>
            <Row>
              <Col>
                <h5 className="mb-2">
                  {`${data.first_name} ${data.last_name}`} 
                </h5>
                <Row className="mt-3 mb-2">
                  <Col>
                    {data.applicant_status && (data.applicant_status==="accepted" || data.applicant_status==="withdrawn") ? (
                      <Fragment></Fragment>
                    ) : (                    
                      <Fragment>
                        <ButtonIcon className="mr-2" tag={Link} color="falcon-info" size="md" icon="envelope" to="#!" iconClassName="fs--2">
                          Request More Info
                        </ButtonIcon>                       
                        <ButtonIcon tag={Link} color="falcon-danger" size="md" icon="ban" to="#!" iconClassName="fs--2">
                          Withdraw Application
                        </ButtonIcon>    
                      </Fragment>
                    )}  
                  </Col>
                </Row>
              </Col>
              <Col xs="auto" className="d-none d-sm-block">
                <h6 className="text-uppercase text-600">
                  <FontAwesomeIcon icon="user" className="ml-2 mr-2" />
                  {data.applicant_status}
                </h6>
              </Col>     
              {data.applicant_status && (data.applicant_status==="accepted" || data.applicant_status==="withdrawn") ? (
                <Fragment></Fragment>
              ) : (
                <AcceptApplicantFC applicantData={data} authToken={token} /> 
              )}
            </Row>
          </CardHeader>
          <CardBody className="border-top">
            <Media>
              <FontAwesomeIcon icon="stream" transform="down-5" className="text-warning mr-2" />
              <Media body>
                <p className="mb-0">Submitted on: {data.timestamp}</p>
                <p className="fs--1 mb-0 text-600">{""}</p>
              </Media>
            </Media>
          </CardBody>
        </Fragment>
      )}
    </Card>
  );
};

