import React, { Fragment, useContext, useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {
  Button,
  Col,
  Row
} from 'reactstrap';
import axios from 'axios';
import { toast } from 'react-toastify';
import AppContext from '../../context/Context';
import { applicantsApi } from '../../config';

const AcceptApplicantFC = ({ applicantData, authToken }) => {
  console.log(">>> AcceptApplicantFC >>> applicantData >>> ", applicantData);
  console.log(">>> AcceptApplicantFC >>> authToken >>> ", authToken);

  function handleClick() {
    updateApplicantBackend(applicantData, authToken);
  }

  const updateApplicantBackend = async (applicantData, authToken) => {
    console.log(">>> updateApplicantBackend >>> applicantData >>> ", applicantData);
    console.log(">>> updateApplicantBackend >>> authToken >>> ", authToken);

    // make sure the token is still valid
    if (!authToken) {
        console.warn(">>> updateApplicantBackend >>> authToken missing");
        toast.warn(`Invalid Auth Token: Token expired`);
        return;
    }      

    axios.defaults.headers.common = {
        'Authorization': 'Bearer ' + authToken,
        'Content-Type': 'application/json',
        'Accept': '*/*',
        'Cache-Control': 'no-cache',
        'Connection': 'keep-alive'
    };      

    try {
        const id = (applicantData.id) ? applicantData.id : undefined;
        
        if (!id) {
            console.log("Applicant ID is undefined");
            toast.error(`Undefined Applicant ID!`);
            return;
        }           

        let applicantUpdates = {
            "applicant_status": "accepted",
        };

        const url = `${applicantsApi}/${id}`;

        const response = await axios.put( url, applicantUpdates );
        console.log(">>> response >>> ", response);

        toast.success(`Applicant Accepted!`);
        setTimeout(() => window.location.reload(), 2000);

    } catch(err) {
        console.warn(">>> handleAcceptedClicked >>> err >>> ", err);
        toast.error(`Failed to Accept the Applicant: ${err}`);
    }
  };

  return (
    <Fragment>
        <Col xs="auto" className="d-none d-sm-block">
            <Button color='falcon-success' size="md" onClick={handleClick}>
                ACCEPT APPLICATION
            </Button> 
        </Col>
    </Fragment>
  );
};

AcceptApplicantFC.propTypes = {
    applicantData: PropTypes.object.isRequired,
    authToken: PropTypes.string.isRequired
};

export default AcceptApplicantFC;
